:root {
  --c1: rgb(1,115,178);
  --c2: rgb(222,143,6);
  --c3: rgb(0,158,115);
  --c4: rgb(219, 155, 207);
  --c1-b: rgb(1,115,178,0.1);
  --c2-b: rgb(222,143,6,0.1);
  --c3-b: rgb(0,158,115,0.1);
  --c4-b: rgb(244,174,231,0.1);
  --link-u: rgba(181, 28, 201, 0.5); /*rgb(71, 219, 219);*/
}

