.border-scribble {
  height: 1.5rem;
  width: 17.5rem;
  background-size: contain;
  background-repeat: no-repeat;
}

#about-header h1, #projects-header h1, #publications-header h1, #classes-header h1 {
  flex-shrink: 0;
  margin-bottom: 0;
  margin-top: 0;
}

