#about-section article {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

#about-section article > p {
  min-width: 20rem;
}

#about-section a {
  color: black;
  text-decoration: none;
  border-bottom: 2px solid rgb(136, 188, 218);
  transition: background 0.1s cubic-bezier(.33,.66,.66,1), color 0.1s cubic-bezier(.33,.66,.66,1);
}

#about-section a:hover, #publications-section td:nth-child(1) a:hover, .pub-links a:hover {
  background: rgb(156, 214, 248);
}

a {
  text-decoration: none;
  color: black;
}

#publications-section table, #projects-section table {
  table-layout: fixed;
  /*border-collapse: collapse;*/
  min-width: 25rem;
  border-spacing: 0 0.5rem;
}

#projects-section tr, #publications-section tr {
  height: 100%;
}

#projects-section th, #publications-section th {
  font-size: 1rem;
  padding-bottom: 0.5rem;
}

#projects-section td, #publications-section td {
  font-size: 0.9rem;
  padding: 0.5rem;
  font-weight: normal;
  border-bottom: 1px solid rgb(39, 39, 39);
  text-align: center;
}

#projects-section th:nth-child(1), #publications-section th:nth-child(1) {
  color: var(--c1);
}

#projects-section th:nth-child(2), #publications-section th:nth-child(2) {
  color: var(--c2);
}

#projects-section th:nth-child(3), #publications-section th:nth-child(3) {
  color: var(--c3);
}

#projects-section th:nth-child(4), #publications-section th:nth-child(4) {
  color: var(--c4);
}

#projects-section td:nth-child(1), #publications-section td:nth-child(1) {
  background-color: var(--c1-b);
  border-radius: 1rem 0 0 1rem;
}

#projects-section td:nth-child(2), #publications-section td:nth-child(2) {
  background-color: var(--c2-b);
}

#projects-section td:nth-child(3), #publications-section td:nth-child(3) {
  background-color: var(--c3-b);
}

#projects-section td:nth-child(4), #publications-section td:nth-child(4) {
  background-color: var(--c4-b);
  border-radius: 0 1rem 1rem 0;
}

/* Links column */
.pub-links {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: space-evenly;
  gap: 0.25rem;
}

.pub-links a {
  text-align: center;
  align-self: stretch;
  padding: 0.25rem;
  background-color: rgb(255, 255, 255);
  border-radius: 0.5rem;
  flex: 0 1;
  border-bottom: 1px solid black;
}

#publications-section td:nth-child(1), #publications-section td:nth-child(2) {
  text-align: left;
}

#projects-section td:nth-child(2) {
  text-align: left;
}

