}

#about-section article {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  font-size: 1.15rem;
}

#about-header, #projects-header, #publications-header, #classes-header {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  gap: 1rem;
}

#about-header img, #projects-header img, #publications-header img, #classes-header img {
  width: 2rem;
  height: 2rem;
}

