main {
  padding: 0 5%;
  overflow: hidden;
  font-family: 'Barlow', sans-serif;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  /*grid-template-rows: auto auto auto auto auto;*/
  gap: 1rem;
  width: 90%;
  justify-content: center;
  align-items: center;
}

#App {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
}

#row-1, #row-2 {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 2rem;
}

#block {
  width: 30rem;
  background-color: white;
  height: 2rem;
}

html {
  /*background: rgba(255, 255, 255, 0.192), url("../img/backgrounds/yellow.png");*/
  background: no-repeat center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  /*text-shadow: 2px 0 #fff, -2px 0 #fff, 0 2px #fff, 0 -2px #fff,
             1px 1px #fff, -1px -1px #fff, 1px -1px #fff, -1px 1px #fff;*/
  cursor: url("../img/cursors/triangle-cursor-fill-small.png"), auto;
}

a {
  cursor: url("../img/cursors/circle-cursor-fill-small.png"), pointer;
}

button {
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

#App header {
  margin: 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  background-color: white;
}

#App header > img {
  position: absolute;
  top: 10px;
  width: 150px;
  height: 150px;
  opacity: 0.15;
}

/*img {
  filter: brightness(0.2) invert(.35) sepia(.75) hue-rotate(200deg) saturate(200%);
}*/

#title-name {
  max-width: 100%;
  cursor: url("../img/cursors/circle-cursor-fill-small.png"), auto;;
  padding: 0.5rem;
  margin: 0.5rem;
  z-index: 99;
}

#App #title-name img {
  max-width: 15rem;
  margin-top: 25px;
}

#App nav {
  gap: 1rem;
  margin: 1rem;
}

nav {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: center;
}

nav img {
  cursor: url("../img/cursors/circle-cursor-fill-small.png"), auto;;
  width: 3rem;
  transition: all 0.2s;
}

nav button:nth-child(1) img:hover {
  filter: blur(2px) brightness(0.5) invert(.7) sepia(.5) hue-rotate(270deg) saturate(200%);
}

nav button:nth-child(2) img:hover {
  filter: blur(2px) brightness(0.5) invert(.7) sepia(.5) hue-rotate(180deg) saturate(200%);
}

nav button:nth-child(3) img:hover {
  filter: blur(2px) brightness(0.5) invert(.7) sepia(.5) hue-rotate(50deg) saturate(200%);
}

#graph-container {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  gap: 1rem;
}

#class-selectors {
  gap: 1rem;
}

#class-selectors > div {
  gap: 0.5rem;
  flex-direction: column;
}

#class-selectors, #class-selectors > div {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  user-select: none;
}

#class-selectors > div > div {
  cursor: url("../img/cursors/circle-cursor-fill-small.png"), auto;;
  padding: 0 0.5rem 0.5rem 0.5rem;
  border-radius: 0.5rem;
  width: 8rem;
  height: 6rem;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

#class-selectors > div > div:hover {
  background-color: var(--c3-b);
}

#class-selectors p {
  border-bottom: 1px solid black;
  font-size: 1.5rem;
  width: 6rem;
  margin: 0;
}

#class-selectors span {
  font-size: 1.15rem;
}

#class-selectors ul {
  list-style-type: none;
  text-align: left;
  padding-left: 0;
  position: absolute;
  transform: translateY(7rem);
  background-color: white;
  padding: 1rem;
  border-radius: 0 0 0.5rem 0.5rem;
  border: 1px solid black;
  margin: 0;
}

#class-selectors li {
  padding: 0.2rem;
  cursor: url("../img/cursors/circle-cursor-fill-small.png"), auto;;
}

#class-selectors li:hover {
  background-color: var(--c3-b);
}

#class-selectors-container {
  display: flex;
  justify-content: left;
  margin-bottom: 1.5rem;
}

.class-label {
  pointer-events: none;
}

.nodisplay {
  display: none;
}

.invisible {
  opacity: 0;
}

#graph-container {
  user-select: none;
}

#socials-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 5rem;
  width: 20rem;
  justify-content: space-evenly;
}

#socials-container i:hover {
  color: var(--c1);
  filter: blur(3px);
}

#mail-tooltip > p {
  display: flex;
  height: 1.25rem;
  padding: 0.5rem;
  margin: 0 0.5rem 0 0;
  text-align: center;
  color: white;
  background-color: black;
  align-items: center;
  border-radius: 0.5rem;
  position: absolute;
  transform: translate(-6.5rem, -4.5rem)
}

#mail-tooltip > div {
  content: '';
  width: .75rem;
  height: .75rem;
  background: black;
  position: absolute;
  transform: translate(3.375rem, -2.6rem) rotate(45deg);
}

footer {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap-reverse;
  color: var(--heading-color);
  margin-top: auto;
  background-color: white;
  padding-top: 1rem;
  width: 100%;
}

footer a, footer a:visited {
  color: var(--heading-color);
}

.fa {
  margin: 0.5rem;
  color: black;
}

.fa-envelope:hover {
  cursor: url("../img/cursors/circle-cursor-fill-small.png"), auto;;
}

#loading-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

#loading-screen img {
  width: 45%;
}